import React, { useEffect, useState } from 'react'
import { installPath } from '../constants/Config'

export interface IInstallStatusContext {
  installStatus: boolean
  setInstallStatus: (installStatus: boolean) => void
}

export const InstallStatusContext = React.createContext<IInstallStatusContext>({
  installStatus: false,
  setInstallStatus: () => {},
})

export interface IInstallStatusProviderProps {
  children?: JSX.Element
}

export const InstallStatusProvider = (props: IInstallStatusProviderProps) => {
  const [installStatus, setInstallStatus] = useState(() => {
    return localStorage.getItem(installPath) !== null
  })
  useEffect(() => {
    if (installStatus === true) localStorage.setItem(installPath, 'installed')
  }, [installStatus])

  return (
    <InstallStatusContext.Provider value={{ installStatus, setInstallStatus }}>
      {props.children}
    </InstallStatusContext.Provider>
  )
}

export const InstallStatusConsumer = InstallStatusContext.Consumer
